// import Login from './views/Login.vue'
const Login = resolve => require(['./views/Login.vue'], resolve)
const NotFound = resolve => require(['./views/404.vue'], resolve)
const Home = resolve => require(['./views/Home.vue'], resolve)
const GlobalSetting = resolve => require(['./views/global/GlobalSetting.vue'], resolve)
const Version = resolve => require(['./views/global/Version.vue'], resolve)
const Cdn = resolve => require(['./views/global/Cdn.vue'], resolve)
const Domain = resolve => require(['./views/global/Domain.vue'], resolve)
const Upload = resolve => require(['./views/global/Upload.vue'], resolve)
const NetworkArticleAudit = resolve => require(['./views/content/NetworkArticleAudit.vue'], resolve)
const NetworkMediaAudit = resolve => require(['./views/content/NetworkMediaAudit.vue'], resolve)
const NetworkArticleAuditDetail = resolve => require(['./views/content/NetworkArticleAuditDetail.vue'], resolve)
const NetworkMediaAuditDetail = resolve => require(['./views/content/NetworkMediaAuditDetail'], resolve)
const PushContentManage = resolve => require(['./views/content/PushContentManage.vue'], resolve)
const PushContentEdit = resolve => require(['./views/content/PushContentEdit.vue'], resolve)
const Comment = resolve => require(['./views/content/Comment.vue'], resolve)
const ArticlePush = resolve => require(['./views/content/ArticlePush.vue'], resolve)
const ArticlePushEdit = resolve => require(['./views/content/ArticlePushEdit.vue'], resolve)

const User = resolve => require(['./views/operation/user/User.vue'], resolve)
const PushMessage = resolve => require(['./views/operation/PushMessage.vue'], resolve)

const ToReview = resolve => require(['./views/finance/ToReview.vue'], resolve)
const RateChange = resolve => require(['./views/finance/RateChange.vue'], resolve)

let routes = [
   {
        path: '/',
        component: Login,
        name: 'Login',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/home',
        redirect: { name: 'GlobalSetting'},
    },
    {
        path: '/',
        redirect: { name: 'GlobalSetting'},
        component: Home,
        name: 'GlobalSettingType',   //GlobalSetting
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/global', component: GlobalSetting, name: 'GlobalSetting' },
            { path: '/version', component: Version, name: 'Version' },
            { path: '/cdn', component: Cdn, name: 'Cdn' },
            { path: '/domain', component: Domain, name: 'Domain' },
            { path: '/Upload', component: Upload, name: 'Upload' },
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: 'ContentManage',     //ContentManage
    //     iconCls: 'fa fa-pencil-square-o',//图标样式class
    //     children: [
    //         { path: '/content', component: NetworkArticleAudit, name: 'NetworkArticleAudit' },
    //         { path: '/content-detail', component: NetworkArticleAuditDetail, name: 'NetworkArticleAuditDetail',hidden: true },
    //         { path: '/video', component: NetworkMediaAudit, name: 'NetworkMediaAudit' },
    //         { path: '/video-detail', component: NetworkMediaAuditDetail, name: 'NetworkMediaAuditDetail',hidden: true },
    //         { path: '/content-manage', component: PushContentManage, name: 'PushContentManage' },
    //         { path: '/content-edit', component: PushContentEdit, name: 'PushContentEdit',hidden: true },
    //         { path: '/comment', component: Comment, name: 'Comment' },
    //         { path: '/article-push', component: ArticlePush, name: 'ArticlePush' },
    //         { path: '/article-push-edit', component: ArticlePushEdit, name: 'ArticlePushEdit',hidden: true }
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: 'OperationManage',     //OperationManage
        iconCls: 'fa fa-user-o',//图标样式class
        children: [
            { path: '/user', component: User, name: 'User' },
            { path: '/push-message', component: PushMessage, name: 'PushMessage' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'FinanceManage',     //FinanceManage
        iconCls: 'el-icon-document',//图标样式class
        children: [
            { path: '/to-review', component: ToReview, name: 'ToReview' },
            { path: '/rate-change', component: RateChange, name: 'RateChange' }
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: 'taskManage',     //任务管理
    //     iconCls: 'el-icon-document',//图标样式class
    // },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


