//         next()
//     }
// })

// const token = '9f53y-rcnCDJIOpDOdSNajo6ujWp0ul7d3nIAT74t-1qYuWe0F_uz8hoUKVVAF4YLUEg2cHS';
// localStorage.setItem('token', token);
// Auth.setToken(token);

Vue.mixin({
    created() {
        if (this.filter) {
            this.filter = this.$queryFilter();
            this.$updateRouter(this.filter);
        }
    },
    methods: {
        $queryFilter(){
            let filter = this.filter || {};
            if (this.$route) {
                let query = this.$route.query;
                for (var i in filter) {
                    if (typeof query[i] !== 'undefined') {
                        if (typeof filter[i] === 'number') {
                            filter[i] = parseInt(query[i])
                        } else {
                            filter[i] = query[i]
                        }
                    }
                }
            }
            return filter;
        },
        $updateRouter(query){
            if(!query){
                query = this.$queryFilter();
            }
            this.$router.replace({
                query: Object.assign({}, query)
            })
        },
        $blockRequest({$elem, req, pendingText = "操作中"}) {
            if (!$elem) {
                $elem = document.createElement('button');
            }
            if (!$elem || $elem._req_block) return;

            let isButton = $elem.nodeName.toLowerCase() === 'button';
            let oText = $elem.innerText;

            if (isButton) $elem.setAttribute('disabled', true);

            $elem.innerText = pendingText;
            $elem._req_block = true;

            return req.fin(() => {
                if (isButton) $elem.removeAttribute('disabled');
                $elem._req_block = false;
                $elem.innerText = oText;
            })
        }
    }
})

new Vue({
    router,
    i18n,
    store,
    render: h => h(App)
}).$mount('#app')





